<template>
  <div class="holder">
    <h2 class="mar-b-2 holder">{{$t('checkout.summary')}}</h2>
    <div
      v-if="productData"
      class="md-layout md-gutter mar-b-1"
    >
      <div class="md-layout-item md-size-60 md-medium-size-100">
        <ul class="list list_border holder">
          <li
            v-for="(item, index) in productData"
            v-bind:key="index"
            class="list__item"
          >
            <div class="flex">
              <div class="width-5">
                <img
                  v-if="item.cover"
                  :src="item.cover.url"
                  :alt="item.cover.fileName"
                >
              </div>
              <div class="flex__stretch mar-l-2">
                <h3 class="mar-b-1 border-bottom">{{item.label}}</h3>
                <div class="md-layout md-gutter">
                  <div class="md-layout-item md-small-size-100">
                    <ul class="list list_border">
                      <li class="list__item md-layout">
                        <h3 class="md-layout-item">{{$t('checkout.quantity')}}</h3>
                        <h3 class="md-layout-item text-right">
                          {{ item.price.quantity }}
                        </h3>
                      </li>
                      <li class="list__item md-layout">
                        <h3 class="md-layout-item">{{$t('basket.totalPrice')}}</h3>
                        <h3 class="md-layout-item text-right">
                          {{ item.price.totalPrice }} {{ getOrderResult.currency.symbol }}
                        </h3>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>

      <div class="md-layout-item md-size-40 md-medium-size-100">
        <div class="holder">
          <div class="holder holder_white mar-b-1">
            <h3 class="mar-b-1 border-bottom">
              {{ $t('checkout.billingAddress') }}
            </h3>
            <div class="mar-b-1">
              {{ buildFullAddressLine() }}
            </div>
          </div>
          <div class="holder holder_white mar-b-1">
            <ul class="list list_border">
              <li class="list__item md-layout">
                <h3 class="md-layout-item">{{$t('basket.totalPrice')}}</h3>
                <h3 class="md-layout-item text-right">
                  {{ getOrderResult.amountTotal }} {{ getOrderResult.currency.symbol }}
                </h3>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="text-center mar-b-1">
      <md-button
        type="submit"
        class="radius-block"
        @click="startPayment()"
      >
        {{$t('checkout.continue2')}}
      </md-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
// import axios from 'axios';
import { mapGetters, mapActions } from 'vuex';

import authRequestMixin from '../../mixins/authRequestMixin';

export default {
  name: 'Summary',
  mixins: [authRequestMixin],
  computed: {
    ...mapGetters(['getAllCartItems', 'getOrderResult']),
  },
  data: () => ({
    contextData: {},
    cartData: {},
    productData: {},
  }),
  created() {
    this.getProduct();
    this.getContext();
    this.updateCartItems([]);
  },
  methods: {
    ...mapActions([
      'addCheckoutOrder',
      'addCheckoutOrderItem',
      'addCheckoutOrderConfirmData',
      'updateCartItems',
    ]),
    startPayment() {
      const orderId = localStorage.getItem('orderId');

      if (orderId) {
        axios
          .post(`http://77.245.20.254/sales-channel-api/v3/checkout/order/${orderId}/pay`,
            {
              finishUrl: 'http://covid123.at/checkout/final',
            },
            {
              headers: {
                'sw-access-key': 'SWSCAUPEWFJVU0FVC20WCNDLUG',
                'sw-context-token': localStorage.getItem('contextToken'),
              },
            })
          .then((response) => {
            window.open(response.data.paymentUrl);
          })
          .catch((e) => {
            // eslint-disable-next-line no-console
            console.error('Error occured on replase order ', e);
            this.$toaster.error(this.$t('notify.oops'));
          });
      }
    },
    getContext() {
      axios
        .get('http://77.245.20.254/store-api/v3/context', {
          headers: {
            'sw-access-key': 'SWSCAUPEWFJVU0FVC20WCNDLUG',
            'sw-context-token': localStorage.getItem('contextToken'),
          },
        })
        .then((response) => {
          this.contextData = response.data;
        })
        .catch((e) => {
          // eslint-disable-next-line no-console
          console.error('Error occured on summary ', e);
          this.$toaster.error(this.$t('notify.oops'));
        });
    },
    getProduct() {
      this.productData = this.getAllCartItems;
    },
    buildFullAddressLine() {
      let resultStr = '';

      if (this.contextData && this.contextData.customer) {
        const userAddress = this.contextData.customer.activeBillingAddress;
        const satutation = this.contextData.customer.salutation.translated.displayName;

        if (userAddress && satutation) {
          resultStr = `${satutation} ${userAddress.firstName} ${userAddress.lastName} ${userAddress.street}, `;
          resultStr += `${userAddress.city}, ${userAddress.zipcode}, ${userAddress.country.translated.name}`;
        }
      }

      return resultStr;
    },
  },
  filters: {
    formatPrice: (value => value.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')),
  },
};
</script>
